import React, { Component } from 'react';
//import Finder from "./components/Finder";
import Finder from "./components/Finder";
import axios from "axios";
import Error from "./components/Error";
import './App.css';

const endpoint = "http://ec2-18-191-219-27.us-east-2.compute.amazonaws.com:3000/validatecode";

class App extends Component {
  constructor(props){
    super();
    
    this.state = {
        password:"",
        key:1, 
        error:null,
        isCalOpen: false
    }

    this.onClick = this.onClick.bind(this);
    this.onChange = this.onChange.bind(this);
    this.onKeyPress = this.onKeyPress.bind(this);

    this.pageClick = this.pageClick.bind(this);
  }

 render(){
    const { key, password, error, isCalOpen } = this.state;
    return (
      <div className="App">
        <div className="page">
          <h1 className="title">Summer Land</h1>
          { key
            ? <div> 
                <Finder
                  accesstoken={key}
                  isCalOpen={isCalOpen}
                />
              </div>
            : <div className="Booker">
                  <p>
                    Welcome to Summerland. A simple applicaiton that allows you to book a campsite at a national park. By crawling recreation.gov we compile a list of open campsites allowing you to "snag" a campsite with way less clicking and waiting. The current password is "summerland". Contact Kevin or Michael with any questions @m.fern93@gmail.com. 
                  </p>
                <form>
                      <input
                          className="input-area"
                          type="text"
                          name="password"
                          placeholder="password"
                          value={password}
                          onChange={this.onChange}
                          onKeyPress={this.onKeyPress}
                      />
                    <div className="button-wrapper"> 
                      <button className="submit-button bttn"
                              onClick={this.onClick}
                              type="button"
                      >
                        Let's Book a Site
                    </button>
                  </div>
                </form>
            </div>
          }<div>{error
            ?<Error/>
            : null
          }  </div>
        </div>
      </div>
    );
  }
  componentDidMount(){
    window.addEventListener('mousedown', this.pageClick, false);
  }

  pageClick(){
    this.setState({isCalShown:false})
  }
  /*
  pageClick(event) {
    if (this.mouseIsDownOnCalendar) {
        return;
    }
  
    this.setState({
        showCal: false
    });
  } */

  onChange(event){
    this.setState({ [event.target.name]:event.target.value });
  }
  
  onClick(){
    let params = JSON.stringify({"password": "summerland"});
    this.setState({key:"password"});
    //let params = JSON.stringify({password:this.state.password});
    /*
    axios({
        method: 'POST',
        url: endpoint,
        data: params,
        config : {headers: { 
                "Access-Control-Allow-Methods": "POST, GET, OPTIONS",
                "Access-Control-Allow-Origin": "http://ec2-18-191-219-27.us-east-2.compute.amazonaws.com:3000/validatecode",
                "Access-Control-Allow-Headers": "Origin, X-Requested-With, Content-Type, Accept,  application/json",
                "Content-Type": 'application/json'
            }}
      }).then((rsp) => {
        console.log(rsp.data);
        this.setState({key:rsp.data})
      }).catch((error) => {
        console.log( error.response )
      }); */
    }

  onKeyPress(event){
    if(event.charCode === 13){
      event.preventDefault();
      this.onClick();
    }
  }
}

export default App;



